.result-box{
    background-color: white;
    BORDER-RADIUS: 10px;
    float: left;
    -webkit-box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.43);
            box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.43);
}
.result-user{
    float: left;
}


.contents{
    margin: 0 20px;
    padding:15px;
    -webkit-box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.43);
            box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.43);
    border-radius: 10px;;
    background-color: white;
}
.img-div{
    width:35%;
    padding: 10px;
    display: inline-block;
}

ul{
    width: 200px;
    margin: 15px;
    padding: 15px;
}
li{
    text-align: left;
    list-style: none;
    padding: 5px;
}
a{
    text-decoration: none;
}

input[type="text"] {
    width:calc(100% - 150px);
    margin:5px;
    margin-top: 35px;
    margin-bottom: 25px;
    -webkit-box-shadow:  0px 0px 15px rgba(0, 0, 0, 0.43);
    box-shadow:  0px 0px 15px rgba(0, 0, 0, 0.43);
    padding:10px;
    border: 0px;
    border-radius: 10px;
    font-size: small;
    color: gray;
}

input[type="submit"] {
    width: 70px;
    height: 40px;
    border: none;
    background: #cb4e4e;
    color: #fff;
    border-radius: 4px;
    -webkit-box-shadow: inset 0 -6px #ab3c3c;
            box-shadow: inset 0 -6px #ab3c3c;
    font-size: small;
}
input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}

::-moz-placeholder{
    color: #ccc;
}
::-webkit-input-placeholder{
    color: #ccc;
}

.main{
    width:100%;
}

.Search-contents{
    display: inline-block;
    width:95%;
}

table{
    table-layout: fixed;
    width: 100%;
}

td:nth-child(2n+1){
    width: 30%;
}
td:nth-child(2n){
    width: 70%;
}
.nodisp{
    display: none;
}

.repos-box{
    position: relative;
}
.timestamp{
    position: absolute;
    top:0;
    right:0;
}
.title{
    display: inline-block;
    font-size: x-large;
    font-weight: 300;
    width: 80%;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}

.repo{
    text-align: left;
}
.repo .title{
    white-space: nowrap;
    width: 65%;
}
.repo-divider{border: 0;
    border-top: 1px solid #f3eded;
}
.proj_link{
    float: right;
    padding: 5px 15px;
    background-color: #e4e3e3;
    position: absolute;
    right: 0;
    bottom: 10px;
    border-bottom: 2px solid #62bfce;
}
.description{
    display: inline-block;
    width: calc(100% - 100px);
}
.fixe{
    overflow: hidden;
}



@media only screen and (max-width: 768px) {
    .result-box{
        margin: 0;
        width:100%;
        position: absolute;
        left: 0;
        -webkit-transition: all 1s;
        -o-transition: all 1s;
        transition: all 1s;
        border-radius: 0;
        -webkit-box-shadow: 0 0 0 black;
                box-shadow: 0 0 0 black;
        z-index:5;
    }
    .onscreen{
        left:-110%;
    }
    .revealer{
        position: fixed;
        z-index: 12;
        top:0;
        left:0;
        margin: 15px;
        font-size: large;
    }
    .result-user{
        margin-top: 10px;
        width: 100%;
    }
    .contents{
        margin:0;
    }
    .img-div{
        width:auto;
    }
}
@media only screen and (min-width: 768px) {
    .result-box{
        width:30%;
    }
    .revealer{
        display: none;
    }
    .result-user{
        width: 70%;
    }
}


